'use client';

import { useState, useEffect, useMemo } from 'react';
import {
  Container,
  Grid,
  Card,
  Text,
  Group,
  Stack,
  Badge,
  Button,
  Title,
  Loader,
  Center,
  Alert,
  TextInput,
} from '@mantine/core';
import {
  IconTrendingUp,
  IconArrowUpRight,
  IconArrowDownRight,
  IconChevronDown,
  IconChevronUp,
  IconHistory,
  IconRotateClockwise,
  IconSearch,
  IconAlertCircle,
} from '@tabler/icons-react';
import { notifications } from '@mantine/notifications';
import {
  useGetBanksQuery,
  useBankConsentMutation,
  useLazyGetBankConsentStatusQuery,
  useGetBankOverviewQuery,
  useSyncBanksMutation,
  useLazyGetAccountTransactionsQuery,
  Transaction,
} from '@/lib/store/api/AuthApi';
import StatisticsChart from './StatisticsChart';

const PAGE_STYLES = {
  background: '#F5F7FA',
  cardBackground: '#FFFFFF',
  paperBackground: '#F8F9FA',
  buttonBlue: '#1E90FF',
  successGreen: '#28a745',
  accentPurple: '#9C27B0',
  accentOrange: '#FF6B35',
  textPrimary: '#1A1A1A',
  textSecondary: '#6C757D',
  borderColor: '#E0E0E0',
} as const;

const formatBalance = (balance: string | number | undefined | null, currency: string = 'RUB'): string => {
  if (balance === undefined || balance === null || balance === '') {
    return '0.00';
  }
  const num = typeof balance === 'string' ? parseFloat(balance) : balance;
  if (isNaN(num)) {
    return '0.00';
  }
  return num.toLocaleString('ru-RU', { minimumFractionDigits: 2, maximumFractionDigits: 2 }) + ' ' + currency;
};

const getBankDisplayName = (bankId: string): string => {
  const bankNames: Record<string, string> = {
    vbank: 'ВТБ',
    sbank: 'Сбербанк',
    abank: 'Альфа-Банк',
  };
  return bankNames[bankId] || bankId;
};

export default function Dashboard() {
  const [consentModalOpen, setConsentModalOpen] = useState(false);
  const [selectedBank, setSelectedBank] = useState<string | null>(null);
  const [clientId, setClientId] = useState('');
  const [consentRequestId, setConsentRequestId] = useState<string | null>(null);
  const [bankSearchQuery, setBankSearchQuery] = useState('');
  const [banksExpanded, setBanksExpanded] = useState(false);
  const [transactionsExpanded, setTransactionsExpanded] = useState(false);
  const [loadedTransactions, setLoadedTransactions] = useState<Transaction[]>([]);
  const [transactionsLoading, setTransactionsLoading] = useState(false);

  const { data: banksData, isLoading: banksLoading } = useGetBanksQuery();
  const [bankConsent] = useBankConsentMutation();
  const [getBankConsentStatus] = useLazyGetBankConsentStatusQuery();
  const { data: accountsData, isLoading: accountsLoading, error: accountsError, refetch: refetchAccounts } = useGetBankOverviewQuery({ refresh: true });
  const [syncBanks] = useSyncBanksMutation();
  const [getAccountTransactions] = useLazyGetAccountTransactionsQuery();

  // Загружаем транзакции для всех счетов
  useEffect(() => {
    if (accountsData?.banks) {
      setTransactionsLoading(true);
      setLoadedTransactions([]); // Очищаем старые транзакции
      const loadPromises: Promise<void>[] = [];

      accountsData.banks.forEach((bank) => {
        (bank.accounts || []).forEach((account) => {
          const promise = getAccountTransactions({
            bankId: bank.bankId,
            accountId: account.accountId,
          }).then((result) => {
            if (result.data?.data?.transaction) {
              const transactions = result.data.data.transaction.map((tx: Transaction) => ({
                ...tx,
                bankId: bank.bankId,
                accountId: account.accountId,
              }));
              setLoadedTransactions((prev) => {
                const newTransactions = [...prev, ...transactions];
                console.log('Loaded transactions for', bank.bankId, account.accountId, ':', transactions.length);
                return newTransactions;
              });
            }
          }).catch((error) => {
            console.error('Error loading transactions:', error);
            // Игнорируем ошибки для отдельных счетов
          });
          loadPromises.push(promise);
        });
      });

      Promise.all(loadPromises).finally(() => {
        setTransactionsLoading(false);
      });
    } else {
      setLoadedTransactions([]);
    }
  }, [accountsData, getAccountTransactions]);

  // Вычисляем общий баланс
  const totalBalance = useMemo(() => {
    if (!accountsData?.banks) return 0;
    let total = 0;
    accountsData.banks.forEach((bank) => {
      (bank.accounts || []).forEach((account) => {
        const balance = account.balance || account.balances?.[0]?.amount || '0';
        const balanceNum = parseFloat(String(balance));
        if (!isNaN(balanceNum)) {
          total += balanceNum;
        }
      });
    });
    return total;
  }, [accountsData]);

  // Собираем все транзакции
  const allTransactions = useMemo(() => {
    const transactionsMap = new Map<string, any>();

    // Добавляем транзакции из accountsData
    if (accountsData?.banks) {
      accountsData.banks.forEach((bank) => {
        (bank.accounts || []).forEach((account) => {
          if (account.transactions && Array.isArray(account.transactions)) {
            account.transactions.forEach((tx: any) => {
              const uniqueKey = `${bank.bankId}_${account.accountId}_${tx.transactionId || tx.id || ''}`;
              if (!transactionsMap.has(uniqueKey)) {
                transactionsMap.set(uniqueKey, {
                  ...tx,
                  bankId: bank.bankId,
                  accountId: account.accountId,
                  uniqueKey,
                });
              }
            });
          }
        });
      });
    }

    // Добавляем загруженные транзакции
    loadedTransactions.forEach((tx) => {
      const uniqueKey = `${tx.bankId || ''}_${tx.accountId || ''}_${tx.transactionId || ''}`;
      if (!transactionsMap.has(uniqueKey)) {
        transactionsMap.set(uniqueKey, {
          ...tx,
          uniqueKey,
        });
      }
    });

    const all = Array.from(transactionsMap.values());
    return all.sort((a, b) => {
      const dateA = new Date(a.date || a.bookingDateTime || a.valueDateTime || 0).getTime();
      const dateB = new Date(b.date || b.bookingDateTime || b.valueDateTime || 0).getTime();
      return dateB - dateA;
    }).slice(0, 10);
  }, [accountsData, loadedTransactions]);

  // Вычисляем доходы и расходы
  const incomeExpense = useMemo(() => {
    let income = 0;
    let expense = 0;

    allTransactions.forEach((transaction: any) => {
      let amount = 0;
      if (typeof transaction.amount === 'object' && transaction.amount?.amount) {
        amount = parseFloat(String(transaction.amount.amount || '0'));
      } else {
        amount = parseFloat(String(transaction.amount || '0'));
      }

      if (!isNaN(amount) && amount !== 0) {
        const isCredit = transaction.creditDebitIndicator === 'Credit';
        if (isCredit) {
          income += Math.abs(amount);
        } else if (transaction.creditDebitIndicator === 'Debit') {
          expense += Math.abs(amount);
        }
      }
    });

    return { income, expense };
  }, [allTransactions]);

  // Фильтруем список банков
  const banksList = useMemo(() => {
    if (!banksData?.banks) return [];
    const filtered = banksData.banks.filter((bankId) =>
      getBankDisplayName(bankId).toLowerCase().includes(bankSearchQuery.toLowerCase()) ||
      bankId.toLowerCase().includes(bankSearchQuery.toLowerCase())
    );
    return banksExpanded ? filtered : filtered.slice(0, 5);
  }, [banksData, bankSearchQuery, banksExpanded]);


  const handleBankConnect = (bankId: string) => {
    setSelectedBank(bankId);
    setConsentModalOpen(true);
  };

  const handleConsentSubmit = async () => {
    if (!selectedBank) return;

    try {
      const result = await bankConsent({
        bankId: selectedBank,
        data: clientId ? { client_id: clientId } : undefined,
      }).unwrap();

      setConsentRequestId(result.requestId);
      setConsentModalOpen(false);
      notifications.show({
        title: 'Запрос отправлен',
        message: result.message,
        color: 'blue',
      });

      // Проверяем статус каждые 2 секунды
      const checkStatus = setInterval(async () => {
        if (!selectedBank || !result.requestId) return;

        try {
          const statusResult = await getBankConsentStatus({
            bankId: selectedBank,
            requestId: result.requestId,
          }).unwrap();

          if (statusResult.status === 'approved') {
            clearInterval(checkStatus);
            notifications.show({
              title: 'Банк подключен',
              message: 'Согласие получено. Данные обновляются...',
              color: 'green',
            });
            setTimeout(() => {
              refetchAccounts();
            }, 1000);
          }
        } catch (error) {
          // Игнорируем ошибки проверки статуса
        }
      }, 2000);

      // Останавливаем проверку через 30 секунд
      setTimeout(() => {
        clearInterval(checkStatus);
      }, 30000);
    } catch (error: any) {
      notifications.show({
        title: 'Ошибка',
        message: error?.data?.message || 'Не удалось отправить запрос',
        color: 'red',
      });
    }
  };

  const handleSyncBanks = async () => {
    try {
      await syncBanks().unwrap();
      notifications.show({
        title: 'Синхронизация запущена',
        message: 'Данные обновляются...',
        color: 'blue',
      });
      setTimeout(() => {
        refetchAccounts();
      }, 2000);
    } catch (error: any) {
      notifications.show({
        title: 'Ошибка',
        message: error?.data?.message || 'Не удалось запустить синхронизацию',
        color: 'red',
      });
    }
  };

  return (
    <Container size="xl" py="xl" style={{ maxWidth: '1400px', margin: '0 auto' }}>
      <Stack gap="lg">
        <Title order={1} size="h2" fw={700} c={PAGE_STYLES.textPrimary}>
          Дашборд
        </Title>

        {/* Баланс, Сбережения и Транзакции в одну строку, Статистика под балансом и сбережениями */}
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(12, 1fr)',
          gap: 'var(--mantine-spacing-lg)',
        }}>
          {/* Баланс */}
          <div style={{ gridColumn: 'span 4' }}>
            <Card
              padding="xl"
              radius="xl"
              style={{
                background: PAGE_STYLES.cardBackground,
                border: `1px solid ${PAGE_STYLES.borderColor}`,
                boxShadow: '0 1px 3px rgba(0, 0, 0, 0.05)',
              }}
            >
              <Stack gap="md">
                <Text size="sm" c={PAGE_STYLES.textSecondary} fw={500}>
                  Общий баланс
                </Text>
                <Title order={1} style={{ fontSize: '2rem', lineHeight: 1.2, color: PAGE_STYLES.textPrimary, fontWeight: 700 }}>
                  {totalBalance > 0 
                    ? totalBalance.toLocaleString('ru-RU', { minimumFractionDigits: 2, maximumFractionDigits: 2 }) + ' ₽'
                    : accountsLoading 
                      ? 'Загрузка...' 
                      : '0.00 ₽'}
                </Title>
                <Group gap="lg" mt="xs">
                  <Stack gap={4}>
                    <Text size="xs" c={PAGE_STYLES.textSecondary}>Доходы</Text>
                    <Text size="md" fw={600} c={PAGE_STYLES.successGreen}>
                      {incomeExpense.income > 0 
                        ? formatBalance(incomeExpense.income.toString(), 'RUB')
                        : '0.00 RUB'}
                    </Text>
                  </Stack>
                  <Stack gap={4}>
                    <Text size="xs" c={PAGE_STYLES.textSecondary}>Расходы</Text>
                    <Text size="md" fw={600} c="#EF4444">
                      {incomeExpense.expense > 0 
                        ? formatBalance(incomeExpense.expense.toString(), 'RUB')
                        : '0.00 RUB'}
                    </Text>
                  </Stack>
                </Group>
              </Stack>
            </Card>
          </div>

          {/* Сбережения */}
          <div style={{ gridColumn: 'span 4' }}>
            <Card
              padding="xl"
              radius="xl"
              style={{
                background: PAGE_STYLES.cardBackground,
                border: `1px solid ${PAGE_STYLES.borderColor}`,
                boxShadow: '0 1px 3px rgba(0, 0, 0, 0.05)',
              }}
            >
              <Stack gap="md">
                <Text size="sm" c={PAGE_STYLES.textSecondary} fw={500}>
                  Сбережения
                </Text>
                <Title order={1} style={{ fontSize: '2rem', lineHeight: 1.2, color: PAGE_STYLES.textPrimary, fontWeight: 700 }}>
                  {totalBalance > 0 
                    ? (totalBalance * 0.1).toLocaleString('ru-RU', { minimumFractionDigits: 2, maximumFractionDigits: 2 }) + ' ₽'
                    : '0.00 ₽'}
                </Title>
                <Group gap="xs" mt="xs">
                  <IconTrendingUp size={16} color={PAGE_STYLES.successGreen} />
                  <Text size="xs" c={PAGE_STYLES.textSecondary}>
                    +2.36% за месяц
                  </Text>
                </Group>
              </Stack>
            </Card>
          </div>

          {/* Транзакции - растягиваются на 2 строки */}
          <div style={{ gridColumn: 'span 4', gridRow: 'span 2' }}>
            <Card
              padding="xl"
              radius="xl"
              style={{
                background: PAGE_STYLES.cardBackground,
                border: `1px solid ${PAGE_STYLES.borderColor}`,
                boxShadow: '0 1px 3px rgba(0, 0, 0, 0.05)',
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
              }}
            >
              <Stack gap="md" style={{ flex: 1 }}>
                <Group justify="space-between" align="center">
                  <Title order={3} size="h4" fw={600} c={PAGE_STYLES.textPrimary}>
                    Транзакции
                  </Title>
                  {allTransactions.length > 5 && (
                    <Button
                      variant="subtle"
                      size="xs"
                      rightSection={transactionsExpanded ? <IconChevronUp size={14} /> : <IconChevronDown size={14} />}
                      onClick={() => setTransactionsExpanded(!transactionsExpanded)}
                      c={PAGE_STYLES.textSecondary}
                    >
                      {transactionsExpanded ? 'Свернуть' : `Все (${allTransactions.length})`}
                    </Button>
                  )}
                </Group>
                
                {transactionsLoading ? (
                  <Center py="md" style={{ flex: 1 }}>
                    <Loader size="sm" />
                  </Center>
                ) : allTransactions.length > 0 ? (
                  <Stack gap="xs" style={{ flex: 1, overflowY: 'auto' }}>
                    {(transactionsExpanded ? allTransactions : allTransactions.slice(0, 5)).map((transaction: any, index) => {
                      const transactionDate = transaction.date || transaction.bookingDateTime || transaction.valueDateTime || transaction.createdAt || transaction.transactionDate;
                      const date = transactionDate ? new Date(transactionDate) : new Date();
                      const day = date.getDate();
                      const monthNames = ['янв', 'фев', 'мар', 'апр', 'май', 'июн', 'июл', 'авг', 'сен', 'окт', 'ноя', 'дек'];
                      const month = monthNames[date.getMonth()] || '';

                      // Обрабатываем разные форматы суммы
                      let amount = 0;
                      if (typeof transaction.amount === 'object' && transaction.amount?.amount) {
                        amount = parseFloat(String(transaction.amount.amount || '0'));
                      } else {
                        amount = parseFloat(String(transaction.amount || transaction.value || '0'));
                      }

                      // Определяем знак на основе creditDebitIndicator
                      const isCredit = transaction.creditDebitIndicator === 'Credit';
                      const amountStr = isCredit
                        ? `+${formatBalance(Math.abs(amount).toString(), transaction.amount?.currency || transaction.currency || 'RUB')}`
                        : `-${formatBalance(Math.abs(amount).toString(), transaction.amount?.currency || transaction.currency || 'RUB')}`;

                      const name = transaction.transactionInformation || transaction.name || transaction.description || transaction.merchantName || 'Транзакция';

                      return (
                        <Group
                          key={transaction.uniqueKey || `${transaction.bankId || ''}_${transaction.accountId || ''}_${transaction.transactionId || transaction.id || index}`}
                          justify="space-between"
                          p="sm"
                          style={{
                            backgroundColor: index % 2 === 0 ? PAGE_STYLES.paperBackground : 'transparent',
                            borderRadius: 'var(--mantine-radius-md)',
                            border: `1px solid ${PAGE_STYLES.borderColor}`,
                          }}
                          wrap="nowrap"
                        >
                          <Group gap="sm" style={{ flex: 1, minWidth: 0 }}>
                            <div
                              style={{
                                width: '40px',
                                height: '40px',
                                borderRadius: '12px',
                                background: isCredit
                                  ? `${PAGE_STYLES.successGreen}15`
                                  : `${PAGE_STYLES.accentOrange}15`,
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                              }}
                            >
                              {isCredit ? (
                                <IconArrowUpRight size={20} color={PAGE_STYLES.successGreen} />
                              ) : (
                                <IconArrowDownRight size={20} color={PAGE_STYLES.accentOrange} />
                              )}
                            </div>

                            <Stack gap={2} style={{ flex: 1, minWidth: 0 }}>
                              <Text size="sm" fw={500} style={{ color: PAGE_STYLES.textPrimary, lineHeight: 1.2 }} truncate>
                                {name}
                              </Text>
                              <Text size="xs" c={PAGE_STYLES.textSecondary}>
                                {day} {month}
                              </Text>
                            </Stack>
                          </Group>

                          <Text
                            fw={600}
                            size="sm"
                            style={{
                              color: isCredit ? PAGE_STYLES.successGreen : PAGE_STYLES.textPrimary,
                              minWidth: '90px',
                              textAlign: 'right',
                              whiteSpace: 'nowrap'
                            }}
                          >
                            {amountStr}
                          </Text>
                        </Group>
                      );
                    })}
                  </Stack>
                ) : (
                  <Center py="xl" style={{ flex: 1 }}>
                    <Stack align="center" gap="xs">
                      <IconHistory size={32} color={PAGE_STYLES.textSecondary} />
                      <Text size="sm" c={PAGE_STYLES.textSecondary}>
                        Нет транзакций
                      </Text>
                    </Stack>
                  </Center>
                )}
              </Stack>
            </Card>
          </div>

          {/* Статистика - под балансом и сбережениями */}
          <div style={{ gridColumn: 'span 8' }}>
            <Card
              padding="xl"
              radius="xl"
              style={{
                background: PAGE_STYLES.cardBackground,
                border: `1px solid ${PAGE_STYLES.borderColor}`,
                boxShadow: '0 1px 3px rgba(0, 0, 0, 0.05)',
              }}
            >
              <Stack gap="md">
                <Group justify="space-between" align="center">
                  <Title order={3} size="h4" fw={600} c={PAGE_STYLES.textPrimary}>
                    Статистика
                  </Title>
                  <Badge variant="light" size="sm" c={PAGE_STYLES.textSecondary}>
                    Последние 7 дней
                  </Badge>
                </Group>

                {/* AreaChart статистика */}
                {chartData && chartData.length > 0 ? (
                  <div>
                    <AreaChart
                      h={280}
                      data={chartData}
                      dataKey="date"
                      type="stacked"
                      withLegend
                      legendProps={{ verticalAlign: 'bottom', height: 50 }}
                      curveType="natural"
                      strokeWidth={2}
                      fillOpacity={0.6}
                      withTooltip
                      withDots={false}
                      withXAxis
                      withYAxis
                      yAxisProps={{
                        domain: [0, 'auto'],
                        tickFormatter: (value: number) => {
                          if (value >= 1000000) return `${(value / 1000000).toFixed(1)}M`;
                          if (value >= 1000) return `${(value / 1000).toFixed(1)}K`;
                          return value.toString();
                        },
                      }}
                      xAxisProps={{
                        tick: { fontSize: 12 },
                      }}
                      series={[
                        { 
                          name: 'Доходы', 
                          color: PAGE_STYLES.buttonBlue,
                          label: 'Доходы'
                        },
                        { 
                          name: 'Расходы', 
                          color: PAGE_STYLES.textPrimary,
                          label: 'Расходы'
                        },
                      ]}
                    />
                    {/* Отладочная информация - можно убрать позже */}
                    {process.env.NODE_ENV === 'development' && (
                      <details style={{ marginTop: '10px', fontSize: '10px', color: '#999' }}>
                        <summary>Debug info</summary>
                        <pre style={{ fontSize: '10px', maxHeight: '200px', overflow: 'auto' }}>
                          {JSON.stringify(chartData, null, 2)}
                        </pre>
                        <div style={{ marginTop: '10px' }}>
                          <strong>Transactions loaded:</strong> {allTransactions.length}
                        </div>
                        <div>
                          <strong>Has data:</strong> {chartData.some(d => d.Доходы > 0 || d.Расходы > 0) ? 'Yes' : 'No'}
                        </div>
                      </details>
                    )}
                  </div>
                ) : (
                  <Center h={280}>
                    <Stack align="center" gap="xs">
                      <Text size="sm" c={PAGE_STYLES.textSecondary}>
                        Нет данных для отображения
                      </Text>
                      <Text size="xs" c={PAGE_STYLES.textSecondary}>
                        Транзакции появятся после синхронизации
                      </Text>
                    </Stack>
                  </Center>
                )}
              </Stack>
            </Card>
          </div>
        </div>

        {/* Мои счета */}
        <Card
          padding="xl"
          radius="xl"
          style={{
            background: PAGE_STYLES.cardBackground,
            border: `1px solid ${PAGE_STYLES.borderColor}`,
            boxShadow: '0 1px 3px rgba(0, 0, 0, 0.05)',
          }}
        >
          <Stack gap="md">
            <Group justify="space-between" align="center">
              <Title order={3} size="h4" fw={600} c={PAGE_STYLES.textPrimary}>
                Мои счета
              </Title>
              <Button
                variant="light"
                size="sm"
                leftSection={<IconRotateClockwise size={16} />}
                onClick={handleSyncBanks}
                loading={accountsLoading}
              >
                Синхронизировать
              </Button>
            </Group>

            {accountsError && (
              <Alert
                icon={<IconAlertCircle size={16} />}
                title="Ошибка загрузки"
                color="red"
                variant="light"
              >
                Не удалось загрузить данные счетов. Попробуйте обновить страницу.
              </Alert>
            )}

            {accountsLoading ? (
              <Center py="xl">
                <Loader size="md" />
              </Center>
            ) : accountsData?.banks && accountsData.banks.length > 0 ? (
              <Group gap="md" align="flex-start">
                {accountsData.banks.map((bank) => (
                  <Card
                    key={bank.bankId}
                    padding="md"
                    radius="md"
                    style={{
                      background: PAGE_STYLES.paperBackground,
                      border: `1px solid ${PAGE_STYLES.borderColor}`,
                      flex: 1,
                      minWidth: '200px',
                    }}
                  >
                    <Stack gap="sm">
                      <Text fw={600} size="sm" c={PAGE_STYLES.textPrimary}>
                        {getBankDisplayName(bank.bankId)}
                      </Text>
                      {(bank.accounts || []).length > 0 ? (
                        <Stack gap="xs">
                          {(bank.accounts || []).map((account) => (
                            <div key={account.accountId || account.id}>
                              <Text size="xs" c={PAGE_STYLES.textSecondary} truncate>
                                {account.accountName || account.nickname || account.accountNumber || 'Счет'}
                              </Text>
                              <Text size="sm" fw={600} c={PAGE_STYLES.textPrimary}>
                                {formatBalance(account.balance || account.balances?.[0]?.amount || '0', account.currency || 'RUB')}
                              </Text>
                            </div>
                          ))}
                        </Stack>
                      ) : (
                        <Text size="xs" c={PAGE_STYLES.textSecondary}>
                          Нет счетов
                        </Text>
                      )}
                    </Stack>
                  </Card>
                ))}
              </Group>
            ) : (
              <Center py="xl">
                <Stack align="center" gap="xs">
                  <Text size="sm" c={PAGE_STYLES.textSecondary}>
                    Нет подключенных банков
                  </Text>
                </Stack>
              </Center>
            )}
          </Stack>
        </Card>

        {/* Подключить банк */}
        <Card
          padding="xl"
          radius="xl"
          style={{
            background: PAGE_STYLES.cardBackground,
            border: `1px solid ${PAGE_STYLES.borderColor}`,
            boxShadow: '0 1px 3px rgba(0, 0, 0, 0.05)',
          }}
        >
          <Stack gap="md">
            <Title order={3} size="h4" fw={600} c={PAGE_STYLES.textPrimary}>
              Доступные банки
            </Title>

            <TextInput
              placeholder="Поиск банка..."
              leftSection={<IconSearch size={16} />}
              value={bankSearchQuery}
              onChange={(e) => setBankSearchQuery(e.target.value)}
              size="sm"
            />

            {banksLoading ? (
              <Center py="md">
                <Loader size="sm" />
              </Center>
            ) : banksList.length > 0 ? (
              <Stack gap="xs">
                {banksList.map((bankId) => (
                  <Group
                    key={bankId}
                    justify="space-between"
                    p="sm"
                    style={{
                      backgroundColor: PAGE_STYLES.paperBackground,
                      borderRadius: 'var(--mantine-radius-md)',
                      border: `1px solid ${PAGE_STYLES.borderColor}`,
                      cursor: 'pointer',
                      transition: 'all 0.2s ease',
                    }}
                    onClick={() => handleBankConnect(bankId)}
                    onMouseEnter={(e) => {
                      e.currentTarget.style.backgroundColor = '#E9ECEF';
                    }}
                    onMouseLeave={(e) => {
                      e.currentTarget.style.backgroundColor = PAGE_STYLES.paperBackground;
                    }}
                  >
                    <Text size="sm" fw={500} c={PAGE_STYLES.textPrimary}>
                      {getBankDisplayName(bankId)}
                    </Text>
                    <Button size="xs" variant="light">
                      Подключить
                    </Button>
                  </Group>
                ))}
                {banksData && banksData.banks.length > 5 && (
                  <Button
                    variant="subtle"
                    size="xs"
                    onClick={() => setBanksExpanded(!banksExpanded)}
                    rightSection={banksExpanded ? <IconChevronUp size={14} /> : <IconChevronDown size={14} />}
                  >
                    {banksExpanded ? 'Свернуть' : `Показать все (${banksData.banks.length})`}
                  </Button>
                )}
              </Stack>
            ) : (
              <Center py="md">
                <Text size="sm" c={PAGE_STYLES.textSecondary}>
                  Банки не найдены
                </Text>
              </Center>
            )}
          </Stack>
        </Card>
      </Stack>

      {/* Модальное окно для подключения банка */}
      {consentModalOpen && selectedBank && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            zIndex: 1000,
          }}
          onClick={() => setConsentModalOpen(false)}
        >
          <Card
            padding="xl"
            radius="md"
            style={{
              background: PAGE_STYLES.cardBackground,
              maxWidth: '400px',
              width: '90%',
            }}
            onClick={(e) => e.stopPropagation()}
          >
            <Stack gap="md">
              <Title order={3} size="h4">
                Подключить {getBankDisplayName(selectedBank)}
              </Title>
              <Text size="sm" c={PAGE_STYLES.textSecondary}>
                Введите client_id (опционально). Если не указать, будет использован сохраненный.
              </Text>
              <TextInput
                label="Client ID"
                placeholder="team052-1"
                value={clientId}
                onChange={(e) => setClientId(e.target.value)}
              />
              <Group justify="flex-end" gap="sm">
                <Button variant="subtle" onClick={() => setConsentModalOpen(false)}>
                  Отмена
                </Button>
                <Button onClick={handleConsentSubmit}>
                  Подключить
                </Button>
              </Group>
            </Stack>
          </Card>
        </div>
      )}
    </Container>
  );
}
